:root {
  --purple: #666afb;
  --grey: #c0c1cf;
  --red: #fb6b6c;
  --pink: #fca6a6;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul li {
  list-style-type: none;
}

body,
body button {
  font-family: "Barlow Semi Condensed";
}

button {
  cursor: pointer;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
}

#root {
  background-color: var(--purple);
}

.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.todo-card {
  min-width: 450px;
  background-color: #ffff;
  border-radius: 5px;
}

.todo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1rem;
  margin-bottom: 0.5rem;
  background-color: #fbfbfb;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.date-wrapper .date {
  color: var(--purple);
  font-size: 35px;
}
.date-wrapper .date span {
  font-weight: 600;
}

.date-wrapper .month {
  font-size: 24px;
  color: var(--grey);
  font-weight: 500;
}

.task-wrapper {
  font-size: 20px;
  color: var(--grey);
}
.task-wrapper span {
  font-weight: 600;
}

.todo-input-form {
  padding: 1rem;
}

.input-wrapper {
  display: flex;
  gap: 10px;
}
.input-wrapper .todo-input {
  flex: 1 1 75%;
  font-size: 24px;
  padding: 5px;
  border: 1px solid rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  outline: none;
}
.input-wrapper .todo-input-submit {
  flex: 1;
  font-size: 30px;
  border-radius: 50%;
  border: none;
  padding: 5px 0;
  background-color: var(--red);
  color: #fff;
  cursor: pointer;
}

.filter-buttons-container {
  display: flex;
  gap: 5px;
  justify-content: space-between;
  padding: 1rem;
}
.filter-buttons-container button {
  flex: 1;
}
.default-filter-button {
  background-color: var(--grey);
  border: none;
  color: #ffff;
  font-size: 1.2rem;
  opacity: 0.4;
}
.filter-button-is-selected {
  background-color: var(--pink);
  border: none;
  color: #fff;
  font-size: 1.2rem;
}

.todo-list {
  padding: 0.5rem 1rem;
}

.todo-list .todo-item-wrapper {
  padding: 0.5rem 0;
}

.todo-list .todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.todo-complete-item {
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-complete-item input[type="checkbox"] {
  margin-right: 1rem;
  -webkit-appearance: none;
  width: 25px;
  height: 25px;
  background-color: var(--grey);
  border: 0;
  border-radius: 50%;
  transition: box-shadow 0.3s;
  cursor: pointer;
}

.todo-complete-item label {
  opacity: 1;
  transition: opacity 0.5s;
}

.todo-is-completed {
  box-shadow: inset 0 0 0 20px var(--red);
}

.todo-complete-item label.todo-is-completed-text {
  opacity: 0.3;
}

.todo-is-not-completed-yet {
  box-shadow: none;
}

.todo-complete-item label {
  font-size: 24px;
}

.todo-button-group button {
  background-color: transparent;
  border: none;
  font-size: 1rem;
  margin-right: 0.5rem;
}

.todo-button-group button .fas {
  margin-right: 0.5rem;
  font-weight: 500;
}

.todo-edit-item form p {
  font-size: 24px;
  margin-bottom: 0.5rem;
}

.todo-edit-item form input {
  font-size: 24px;
  padding: 5px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid var(--grey);
  margin-bottom: 0.5rem;
  outline: none;
}

.todo-edit-item form .edit-button-group {
  display: flex;
  gap: 1rem;
}

.todo-edit-item form .edit-button-group button {
  flex: 1;
}

.nothing-to-do {
  padding: 1rem;
  text-align: center;
  font-size: 25px;
}
